.auth-inner {
  margin: 0 auto;
  
  background: linear-gradient(rgb(141, 141, 141), rgba(0, 0, 0, 0.9));
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  
  transition: all .3s;

  @media (min-width: 1300px) {
    width: 455px;
    margin-top: 20px;
  }

}

.label {
  color: #ffffff;
}


.btn-signup{
  position: relative;
  display: inline-block;
  width: 144px;
  height: 44px;
  // height: 440px;
  padding: 0;
  margin: 0;
  color: #ffffff;


  border: none;
  background: transparent;

  &:hover{
    border-radius: 5px;
    background: linear-gradient(rgb(68, 68, 68), rgba(0, 0, 0, 0.9));
    color: #FFFFFF;
    cursor: pointer;
    // cursor: ;
  }

  &:focus{
    
    background: #474747;
    outline: none;
  }

  &--menu{

    @media (min-width: 1300px) {
      display: none;
    }

    &>span{
      display: block;
      width: 40px;
      height: 2px;
      margin: 0 auto;

      background: #FFFFFF;
      box-shadow: 0 12px 0 #FFFFFF, 0 -12px 0 #FFFFFF;
      transition: 0.5s;
    }

    &:focus,
    &:hover{
      &>span {
        background: #B78A3F;
        box-shadow: 0 16px 0 #B78A3F, 0 -16px 0 #B78A3F;
      }
    }
  }

  &--close{
    transition: 0.5s;
    &::after,
    &::before{
      position: absolute;
      top: 0;

      content: "";

      width: 2px;
      height: 45px;

      background: #FFFFFF;
      transition: 0.5s;
    }

    &::after{
      transform: rotate(45deg);
    }

    &::before{
      transform: rotate(-45deg);
    }
  }

  &--fixed{
    //position: fixed;
    top: 20px;
    right: 20px;
  }

  &:focus,
  &:hover{
    transform: rotate(360deg);
    &::after,
    &::before {
      background: #B78A3F;
    }
  }
}

