/* /////////////////////style.css/////////////////////////////////////// */

.container-slider {
  width: auto;
  background: #C0C0C0;
}

@font-face {
font-family: HelveticaNeueCyr;
src: url(./fonts/helveticaneuecyr-light.ttf);
}
/* global */
* {
margin: 0;
padding: 0;
box-sizing: border-box;
}
img, object, video, embed {
max-width: 100%;
}
.content-container {
  max-width: 1280px;
  margin: 0 auto;
}

/* section general */
.global {
  /* height: 100vh; */
  position: relative;
  background: #F4F6F6;
}
.header {
  position: absolute;
  z-index: 100;
  width: 100%;
  background-image: linear-gradient(to top, rgba(252, 227, 138, 0.8) 0%, rgba(255, 255, 255, 0.9) 100%);
}
.logo-partner-wrapper {
  padding: 10px 20px;
  text-align: right;
}
.logo-partner { 
  margin-top: 20px; 
  max-width: 350px;
}
.logo-wrapper {
  display: flex;
  align-items: center;
}
.logo {
  margin: 0;
  align-items: flex-end;
}
.logo-title {
  font-family: Montserrat;
font-size: 40px;
font-weight: 600;
  color: #2F4F4F;
  text-transform: uppercase;
  line-height: 40px;
  text-align: center;
  padding-top: 20px;
}
.logo-title-highlight {
font-size: 28px;
font-weight: 500;
  text-transform: none;
}

#top-nav{
  position: fixed;
  z-index: 100;
  width: 100%;
}
.nav-btn,
#nav-toggle {
display: none;
}

.header-nav-wrapper {
  position: sticky;
  top: 100;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: rgba(255, 215, 0, 1);
  border-bottom: 1px solid #cecece;
}
.header-nav {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.header-nav-list {
  display: flex;
}
.header-nav-item {
  list-style: none;
}
.header-nav-item .fa {
  margin-right: 6px;
}
.header-nav-link {
  text-decoration: none;
  color: #2F4F4F;
  font-family: Montserrat;
font-size: 14px;
font-weight: 600;
  text-transform: uppercase;
  padding: 10px 20px;
}
.nav-link-active,
.header-nav-link:hover {
  color:#fff;
  background-color: #2F4F4F;

}
.nav-right:not(:first-child).header-nav-item .header-nav-link:hover {
  background-color: transparent;
  color: #2F4F4F;
}
#map {
  height: 100vh;
}

/* section gallery */
.gallery {
  height: 500px;
  background-color: #C0C0C0;
}
.facts-title,
.gallery-title {
  font-family: Montserrat;
  font-size: 28px;
  color:  #5a6164;
font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  padding: 50px 0 30px;
}
.gallery-title {
  color: rgba(255, 255, 255, 0.9);
}

/* section facts */
.facts .content-container {
  margin-bottom: 50px;
}
.facts-img-right {
  float: right;
  margin: 5px 0 2em 2em;
}
.facts-img-left {
  float: left;
  margin: 5px 2em 2em 0;
}
.clearfix {
  overflow: auto;
}
.facts-content {
  font-family: Roboto;
font-size: 17px;
  line-height: 26px;
  margin-bottom: 15px;
  text-align: justify;
  color: #5a6164;
}

/* section footer*/
.footer {
  background-color: #ededed;
  color: #5a6164;
}
.footer .content-container {
  display: flex;
  justify-content: space-between;
  padding: 50px 0;
}
.footer-nav-list {
  list-style: none;
}
.footer-nav-item {
  margin-bottom: 15px;
}
.footer-nav-link {
font-family: Roboto;
font-size: 17px;
  line-height: 20px;
  text-decoration: none;
  color: #5a6164;
  text-transform: uppercase;
}
.footer-nav-link:hover {
  color: #000;
}
.footer-content-wrapper {
  width: 50%;
}
.footer-content-title,
.footer-nav-title,
.footer-form-title {
  font-family: Roboto;
  font-size: 20px;
  margin-bottom: 15px;
  text-transform: uppercase;
}
.footer-content {
  font-family: Roboto;
font-size: 17px;
  line-height: 24px;
  margin-bottom: 10px;
  text-align: justify;
}
.footer-sidebar-right {
  display: flex;
  flex-direction: column;
  width: 20%;
}
.footer-form {
  margin-bottom: 30px;
  width: 100%;
}
.footer-input {
  border: 1px solid #C0C0C0;
  outline: none;
  padding: 5px;
  font-family: Roboto;
font-size: 17px;
  color: #5a6164;
  width: 100%;
}
.footer-social {
  display: flex;
}
.footer-social-link {
  width: 40px;
  height: 40px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  border-radius: 50%;
}
.footer-social-link:not(:last-child) {
  margin-right: 5px;
}
.link-facebook {
  background-color: #365899;
}
.link-twitter {
  background-color: #1da1f2;
}
.link-youtube {
  background-color: #cc181e;
}
.link-linkedin {
  background-color: #00a0dc;
}
.footer-copyright {
  padding: 10px 0;
  text-align: center;
}
.footer-copyright-link {
text-decoration: none;
}
.footer-copyright-link:hover .copyright {
  color: #000;
}
.copyright {
font-family: HelveticaNeueCyr ;
font-size: 14px;
color: #2F4F4F;
}

/* modal feedback */
.feedback-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  z-index: 200;
  pointer-events: none;
  margin: 0;
  padding: 0;
  display: none;
}
.feedback-modal:target {
  display: block;
  pointer-events: auto;
  overflow-y: auto;
}
/* кнопка "Закрыть" */
.close {
  position: absolute;
  top: 0;
  right: 5px;
  font-family: sans-serif;
  font-size: 48px;
  font-weight: 100;
  color: #fff;
  text-shadow: 0 1px 0 #000;
  text-decoration: none;
}
.close:focus, .close:hover {
  color: #5a6164;
  cursor: pointer;
}
/**/
.feedback {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  z-index: 200;
}
.feedback-form-wrapper {
  border: 3px solid rgba(255, 215, 0, 1);
  max-width: 100%;
  background-color: rgba(255, 255, 255, 1);
}
.feedback-title {
  background-color: rgba(255, 215, 0, 1);
  padding: 10px 20px;
  color: #2F4F4F;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}
.form-input-title {
  display: block;
  width: 100%;
  padding: 10px 20px;
  color: #2F4F4F;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
}
.form-input-wrapper {
  display: flex;
  padding: 15px 0;
}
.form-input {
  margin-top: 10px;
  font-family: Montserrat;
  font-size: 14px;
  padding: 7px;
  width: 100%;
  border: 1px solid #cecece;
  border-radius: 5px;
}
.form-message {
  margin-top: 10px;
  font-family: Montserrat;
  font-size: 14px;
  padding: 5px;
  border: 1px solid #cecece;
  max-width: 100%;
}
.form-input-reset {
  background-color: rgba(255, 215, 0, 1);
  color: #2F4F4F;
  padding: 10px 20px ;
  border: none;
  outline: none;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  position: relative;
  bottom: 16px;
  cursor: pointer;
  border-radius: 5px;
}
.form-input-submit {
  background-color: rgba(255, 215, 0, 1);
  color: #2F4F4F;
  padding: 10px 20px ;
  border: none;
  outline: none;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  margin: 20px;
  cursor: pointer;
  border-radius: 5px;
}
.form-check {
  padding: 0 20px ;
}
.form-check .form-input {
  margin-top: 0px;
}
.check-img {
  margin-right: 10px;
}

/* modal entry */
.entry-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  z-index: 200;
  pointer-events: none;
  margin: 0;
  padding: 0;
  display: none;
}
.entry-modal:target {
  display: block;
  pointer-events: auto;
  overflow-y: auto;
}

.entry {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  z-index: 200;
}
.entry-form-wrapper {
  border: 3px solid rgba(255, 215, 0, 1);
  max-width: 100%;
  background-color: rgba(255, 255, 255, 1);
}
.entry-title {
  background-color: rgba(255, 215, 0, 1);
  padding: 10px 20px;
  color: #2F4F4F;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}


/* modal registration */
.registration-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  z-index: 200;
  pointer-events: none;
  margin: 0;
  padding: 0;
  display: none;
}
.registration-modal:target {
  display: block;
  pointer-events: auto;
  overflow-y: auto;
}

.registration {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  z-index: 200;
}
.registration-form-wrapper {
  border: 3px solid rgba(255, 215, 0, 1);
  max-width: 100%;
  background-color: rgba(255, 255, 255, 1);
}
.registration-title {
  background-color: rgba(255, 215, 0, 1);
  padding: 10px 20px;
  color: #2F4F4F;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}


/* media request --------------------------------*/
