      line-height: 14px;
  }
  .footer-content-title,
  .footer-nav-title,
  .footer-form-title {
      font-size: 18px;
      margin-bottom: 12px;
  }
  .footer-content {
      font-size: 15px;
      line-height: 20px;
  }
}
@media screen and (max-width: 900px){
  .content-container {
      max-width: 850px;
  }
  .logo-title {
      font-size: 24px;
      line-height: 24px;
  }
}
@media screen and (max-width: 600px){
  .logo-partner-wrapper {
     padding: 20px 0 0;
     text-align: center;
  }
  .logo-partner {
      max-width: 300px;
  }
  .header-logo-wrapper {
      display: flex;
      flex-direction: column;
  }
  .logo-wrapper {
      flex-direction: column;
  }
  .logo-title {
      font-size: 20px;
      line-height: 16px;
      padding: 10px 0 15px;
  }
  .logo-title-highlight {
      font-size: 15px;
      line-height: 10px;
  }

/* nav-toogle */
#nav-toggle {
  opacity: 0;
}
#nav-toggle:checked ~ .nav-btn > span {
  transform: rotate(45deg);
}
#nav-toggle:checked ~ .nav-btn > span::before {
  top: 0;
  transform: rotate(0);
}
#nav-toggle:checked ~ .nav-btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
#nav-toggle:checked ~ .header-nav-list {
  visibility: visible;
  right: 0;
}
.nav-btn {
  display: flex;
  align-items: center;
  position: absolute;
  top: 6px;
  right: 30px;
  width: 26px;
  height: 26px;
  cursor: pointer;
  z-index: 10;
}
.nav-btn > span,
.nav-btn > span::before,
.nav-btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition-duration: .25s;
}
.nav-btn > span::before {
  content: '';
  top: -8px;
}
.nav-btn > span::after {
  content: '';
  top: 8px;
  }
  /* end nav-toogle*/
  
  .header-nav-wrapper {
      height: 40px;
  }
  .header-nav {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 0;
      max-width: 600px;
  }
.header-nav-list {
  display: inline-block;
  position: relative;
  visibility: hidden;
  top: 40px;
  right: -100%;
      width: 100%;
      padding: 0;
      text-align: left;
      background: #ededed;
  transition-duration: .25s;
      z-index: 10;
      border-bottom: 1px solid #cecece;
  }
  .header-nav-list:not(:last-child){
      border-right: 1px solid #cecece;
  }
  .header-nav-link {
      display: inline-block;
      width: 100%;
  }
  .header-nav-link:hover {
      background-color: #456b6b;
  }
  .gallery {
      height: 360px;
      padding: 0 20px;
  }
  .facts {
      background-color: #fff;
      padding: 0 20px;
  }
  .facts-img-left,
  .facts-img-right {
      float: none;
      display: block;
      margin: 10px auto;
  }
  .facts-title,
  .gallery-title {
      font-size: 18px;
      padding: 40px 0 20px;
  }
  .footer {
      padding: 0 20px;
  }
  .footer .content-container {
      display: flex;
      flex-direction: column;
      width: 100%;
  }
  .footer-content-wrapper {
      width: 100%;
      margin-bottom: 20px;
  }
  .footer-content-title {
